body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  padding: 0px;
  margin: 0px;
}

.cbalink {
  visibility: hidden;
}

span {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 40px;
  line-height: 40px;
}

.ButtonMinus span {
  top: 40%;
}

.Module {
  position: relative;
  margin: 60px;
  display: inline-block;
}

.GoodTable {
  border: 1px solid #48AAE6;
  margin: 1px;
  position: relative;
}

td {
  border: 1px solid #48AAE6;
  width: 50px;
  min-width: 50px;
  height: 50px;
  background: #48AAE6;
}

.OperationButton {
  width: 50px;
  height: 50px;
  margin: 1px;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 1s;
          transition-duration: 1s;
  opacity: 1;
  text-align: center;
  vertical-align: middle;
  color: white;
  font-weight: bold;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: absolute;
}

.OperationButton:hover{
  cursor: pointer;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  opacity: 0.7;
}

.ButtonMinus{
  border: 2px solid #B20000;
  background: #B20000;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  visibility: hidden;
}

.ButtonPlus{
  border: 2px solid #F3A500;
  background: #F3A500;
}

.BtnLeft {
  right: 100%;
  margin-top: 2px;
  top: 2px;
}

.BtnTop {
  bottom: 100%;
  left: 2px;
  margin-left: 2px;
}

.BtnRight {
  left: 100%;
  margin-top: 4px;
}

.BtnBottom {
  top: 100%;
  margin-left: 4px;
}
